body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  

* {
    margin: 0;
  }
  
  
  body {
    background-color: #f5f6fa;
  
  
  }
  .app-left {
    flex: 0.9 1;
  }
  
  .app {
    display: flex;
    justify-content: space-evenly;
    background-color: #f5f6fa;
    padding: 20px;
  }
  .app-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    justify-content: space-between;
  }
  
  .app-header h1 {
    color: #fc3c3c;
    font-size: 2rem;
  }
  .app-stats {
    display: flex;
    justify-content: space-between;
  }
  .app-information > h3 {
     color: #6a5d5d;
     font-weight: 400;
     font-size: 1.5rem;
     margin-bottom: 1rem;
  }
  .app-information > h3:last-of-type  {
    margin-top: 1rem;
  }
  @media screen and (max-width: 990px) {
    .app {
       flex-direction: column;
       
    }
  }

.infoBox {
    flex: 1 1;
}
.infoBox:not(:last-child) {
    margin-right: 10px;
}
.infoBox-selected {
    border-top:  10px solid lime;

}
.infoBox-red {
    border-color: red;
}
.infoBox-cases-green {
    color: lightgreen !important;
}
.infoBox-cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.infoBox-total {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}
.map {
    height: 500px;
    background-color: #ffff;
    padding: 1rem;
    margin-bottom: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0,0,0, 0.5);

}

.map .leaflet-container {
    height: 100%;
    border-radius: 12px;
}

.info-flag img {
    width: 100px;
    border-radius: 5px;
}
.info-name {
    font-size: 20px;
    font-weight: bold;
    color: #5555;
}
.info-container {
     width: 150px;
}

.info-flag {
    height: 80px;
    width: 100%;
    background-size: cover ;
    border-radius: 8px;
}


.info-confirmed,
.info-recovered,
.info-deaths {
    font-size: 16px;
    margin-top: 5px;
}
.table {
    margin-top: 20px;
    overflow: scroll;
    overflow-x: hidden;
    color: #6a5d5d;
    background-color: white;
    height: 400px;

}
.table tr{
    display: flex;
    justify-content: space-between;
}

.table td {
    padding: .5rem;
    border: none;
}

.table tr:nth-last-of-type(odd) {
    background-color: #f3f2f8;
}

